import React, {useState} from 'react'
import { useStaticQuery, graphql,Link } from 'gatsby'
import GoogleMapReact from 'google-map-react'
import PropertyItem from '../../../../Global/Pages/PropertyList.component'
import Pagination from '../../../../Global/Pages/Pagination.component'
import AdvanceSearchWidgetSidebar from '../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'

const HalfMapV4Section=()=> {
    const [SideBarShow, setSideBarShow] = useState(false);
    const [SideBarMainShow, setSideBarMainShow] = useState(false);
    const handleMainSidebar=()=>{
        setSideBarMainShow(!SideBarMainShow)
        document.querySelector(".body_content").classList.toggle('translated')
    }
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
            document.querySelector('body').classList.toggle('body_overlay')
            document.querySelector('#mobilesidebar').classList.toggle('sidebar_ml0')
    }
    const data = useStaticQuery(graphql`
        query{
            fp1: file(relativePath: {eq: "property/fp1.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp2: file(relativePath: {eq: "property/fp2.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp3: file(relativePath: {eq: "property/fp3.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp4: file(relativePath: {eq: "property/fp4.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp5: file(relativePath: {eq: "property/fp5.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp15: file(relativePath: {eq: "property/fp15.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp16: file(relativePath: {eq: "property/fp16.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp17: file(relativePath: {eq: "property/fp17.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp18: file(relativePath: {eq: "property/fp18.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp19: file(relativePath: {eq: "property/fp19.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp20: file(relativePath: {eq: "property/fp20.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
        }`)
    return (
        <div className="body_content">
       <section id="feature-property" className="our-listing bgc-f7 pt0 pb0">
		<div className="container-fluid">
			<div className="row">
                <div className="col-lg-12">
                        <div className="listing_sidebar dn db-991">
                            <div id="mobilesidebar"className="sidebar_content_details style3 ">
                                <div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
                                    <div className="sidebar_advanced_search_widget">
                                        <h4 className="mb25">
                                        Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                        </Link>
                                        </h4>
                                        <AdvanceSearchWidgetSidebar index="1" style2/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
            </div>
            <div className="row">
                <div className="col-sm-12">
                <div className="listing_sidebar dn-991">
                    <div id="mainsidebar" className="sidebar_content_details is-full-width style2">
                        <div className="sidebar_listing_list style2 mb0">
                            <div className="sidebar_advanced_search_widget">
                                <h4 className="mb25">Advanced Search</h4>
                                <AdvanceSearchWidgetSidebar index="2" style2/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
			<div className="row">
				<div className="col-lg-12">
					<div className="sidebar_switch mobile_style dn db-991 mt30 mb0">
						<div id="main2">
							<span id="open2" className="flaticon-filter-results-button filter_open_btn style4" onClick={handleSidebar}> Show Filter</span>
						</div>
					</div>
				</div>
				<div className="col-xl-3 p0">
					<div className="half_map_area_content">
						<div className="row">
                            <div className="grid_list_search_result mt30">
                                <div className="col-sm-12 col-md-4 col-lg-7">
                                    <div className="left_area tac-xsd">
                                        <p>9 Search results</p>
                                    </div>
                                </div>
                                <div className="col-sm-12 col-md-8 col-lg-5 pl0 pr0">
                                    <div className="half_map_advsrch_navg style3 text-right tac-xsd">
                                        <ul className="mb0 mt10 mb10-767">
                                            <li className="list-inline-item"><a className="hvr-text-thm" href="#"><span className="fa fa-th-large"></span></a></li>
                                            <li className="list-inline-item"><a className="hvr-text-thm" href="#"><span className="fa fa-th-list"></span></a></li>
                                            <li className="list-inline-item"><a className="hvr-text-thm" href="#"><span className="flaticon-more"></span></a></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
						</div>
						<div className="row">
                            <div className="col-sm-6 col-lg-6 col-xl-12">
                                <PropertyItem listfor='For Rent'
                                featured
                                price="13000"
                                category="Appartment"
                                title="Renovated Apartment"
                                >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg}alt="fp2.jpg"/>
                                    </div>
                                </PropertyItem>
                            </div>
                            <div className="col-sm-6 col-lg-6 col-xl-12">
                                <PropertyItem listfor='For Rent'
                                    price="13000"category="Villa"
                                    title="Gorgeous Villa Bay View"
                                    >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg}alt="fp2.jpg"/>
                                    </div>
                                </PropertyItem>
                            </div>
                            <div className="col-lg-12 mb30">
                                    <Pagination/>
                                </div> 
                     </div>
					</div>
				</div>
           
                <div className="col-xl-9">
                <div className="sidebar_switch style text-right dn-991">
							<div id="main">
								{!SideBarMainShow ? 
                                    <span id="open3" className="showFilter filteropen flaticon-filter-results-button sidebarClosed" onClick={handleMainSidebar}>Show Filter</span>
                                        :
                                    <span id="open3" className="showFilter filteropen flaticon-close sidebarOpended" onClick={handleMainSidebar}>Hide Filter</span>
                                }
                            </div>
						</div>
					<div className="half_map_area">
						<div className="home_two_map style2">
                        <div className="map-canvas skin2 half_style" style={{position: "relative", overflow: "hidden"}}>
                           <GoogleMapReact
						bootstrapURLKeys={{ key: 'AIzaSyAAz77U5XQuEME6TpftaMdX0bBelQxXRlM' }}
						defaultCenter={{
										lat: 51.510280,
										lng: -0.084028
										}}
						defaultZoom={14}
						>
							<div
								>
									Awesome Place
							</div>
        			</GoogleMapReact>
                    </div>
						</div>
					</div>
				</div>
				 </div>
        </div>
	</section>
    </div>
    )
}

export default HalfMapV4Section
